import { uniRate } from '@dcloudio/uni-ui';
export default {
	components: {
		uniRate
	},
	data() {
		return {
			form: {
				id: null,
				star: 5
			},
			state: 0
		}
	},
	onLoad(option) {
		this.form.id = option.id
		this.state = option.state
	},
	methods: {
		starChange(e) {
			this.form.star = e.value
		},
		submit() {
			this.$http.post('order.review', this.form).then(res => {
				uni.redirectTo({
					url: '/pages/order/order?state=' + this.state
				})
			})
		}
	}
}
